import { createSelector } from "@reduxjs/toolkit";

export const getContacts = state => state.contacts.contactsList; 
export const getIsLoading = state => state.contacts.isLoading;
export const getError = state => state.contacts.error;
export const getFilter = state => state.filter.name; 
export const getVibleContacts = createSelector([getContacts, getFilter], (contacts, filter) => {
    const normalizeFilter = filter.toLocaleLowerCase();
    const findContacts = contacts.filter(contact => contact.name.toLocaleLowerCase().includes(normalizeFilter));
    return findContacts;
})


